import React from "react";
import { NavLink } from "react-router-dom";

import {getAuthToken, getAuthTrainerId} from "../Util/Authentication";
import {Icon} from "../asset/js/icon";

const activeStyle = {
	color: "#09D3AC",
};

const defaultStyle = {
	textDecoration: "none",
	color: "black",
};

class Navigation extends React.Component {
	constructor(props) {
		super(props);
		this.state={
			centerName : '',
			userImage: {
				imageSrc:  'data:image/png;base64,' ,
				setImageSrc: '',
			},
			manageList: [
				{
					title:'스케줄', icon: <Icon.ic24Schedule/>, router: '/schedule/member', active: true,
					subMenu: [
						{title:'내 회원', router: '/schedule/member', active: false, },
						{title:'트레이너', router: '/schedule/trainer', active: false},
					]
				},
				{
					title:'회원 관리', icon: <Icon.ic24MemberManage/>, router: '/manage', active: false,
					subMenu: [
						{title:'회원 목록', router: '/manage', active: false},
						{title:'회원 등록', router: '/register', active: false},
					]
				},
			],
		};

	}

	onLogout() {
		localStorage.clear();
		window.location.replace('/');
	}

	render() {
		const {centerName, userImage, manageList} = this.state;

		if(!getAuthToken()) {
			return <>11</>;
		}
		return (
			<>
				<div className={'aside'}>
					<div className={'aside_header'}>
						<h1 className={'company'}>
							{ <Icon.logoSmallGo/>}
							{ <Icon.logoSmallGym/>}
							{ <Icon.logoSmallAdmin className={'logo_admin'} />}
						</h1>
						<div className={'profile'}>
							<div className="thumb_area">
								<div className={'thumbnail'}>
									<img src={ userImage.setImageSrc !== '' ? userImage.setImageSrc : userImage.imageSrc} alt=""/>
								</div>

								<input type="file" id={'userProfile'} className={'input_file'} onChange={(e) => { this.encodeFileToBase64(e.target.files[0]);}} />
								<label htmlFor="userProfile" className={'label_file'}> <Icon.ic12Pencil/> </label>
							</div>

							<div className={'information'}>
								<div className={'name'}>
									{getAuthTrainerId() || '로그인 해주세요.'}
								</div>
								<p className={'description'}>
									{/*{getAuthTrainerId() || '로그인 해주세요.'}*/}
								</p>
								<button type={'button'} className={'btn_setting'} onClick={this.onLogout}>
									<Icon.ic14Setting/> 계정설정
								</button>
							</div>
						</div>
					</div>
					<div className={'aside_content'}>
						<ul className={'menage_list'}>
							{manageList.map((value, index) =>
								<li key={`manage-${value.router}`} className={'item'}>
									<div className="navigation">
										{ value.subMenu &&
										<>
											{value.subMenu.map((value, index) =>
												<NavLink to={value.router} className={'sub_link'}>
													{value.title}
												</NavLink>
											)}
										</>
										}
										<NavLink to={value.router} activeClassName={'active'} className={'main_link'}  exact>
											{value.icon}
											{value.title}
										</NavLink>
									</div>


								</li>
							)}
						</ul>
					</div>
				</div>
			</>
		);
	}
}

export default Navigation;
